

const initialState = {
   email:"",
   isLoggedIn:false,
   token:"",
   domain:""
}
const Authentication = (state = initialState, action) => {
  switch (action.type) {
    case "SIGN_IN":
      window.localStorage.setItem("_token",action.payload.token)
      return action.payload;
    case "SIGN_OUT":
     
      window.localStorage.removeItem('_token')
      console.log(  window.localStorage.getItem("_token"),'dispatiching?')
      return {
        token:"",
         email:"",
         isLoggedIn:false,
         domain:"",
      };
    default:
      return state;
  }
};

export default Authentication;
