import { Button, Col, Row, Form } from "react-bootstrap";
import "./Register.css";
import React, { useEffect, useState } from "react";
import axios from "axios";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import { useHistory } from "react-router-dom";
const MySwal = withReactContent(Swal);

function Register() {
  const history = useHistory()
  const [validated, setValidated] = useState(false);
  // const [values, setValues] = useState(initialState);

  const [businessName, setBusinessName] = useState("");
  const [businessAddress, setBusinessAddress] = useState(null);
  const [countryOrigin, setCountryOrigin] = useState(null);
  const [contactNumber, setContactNumber] = useState(null);
  const [firstName, setFirstName] = useState(null);
  const [lastName, setLastName] = useState(null);
  const [emailAddress, setEmailAddress] = useState(null);
  const [countryOpt, setCountryOpt] = useState([]);

  
  const clearFields = ()=> {
    setBusinessName("")
    setCountryOrigin("")
    setContactNumber("")
    setFirstName("")
    setLastName("")
    setEmailAddress("")

  }
  // const handleChange = (e) => {
	// 	setValues({ ...values, [e.target.name]: e.target.value });
	// 	// console.log(e.target.name, " ----- ", e.target.value);
	// };
  const signUp = (event) => {
    const form = event.currentTarget;
    console.log(form,"this is form")
    if (form.checkValidity() === false) {
      // event.preventDefault();
      // event.stopPropagation();
      return false
    }
    setValidated(true);
    // console.log(this," asdasdsad")
    // return
    const data = {
      // company details
      company: businessName,
      country: "PH",
      address: emailAddress,
      contact: 123456789,
      // contact person details
      fname: firstName,
      lname: lastName,
      phone: contactNumber,
      email: emailAddress,
    };
    axios({
      url: `${process.env.REACT_APP_API_URL}/api/invrel/signup`,

      method: "post",

      data: data,
    })
      .then((response) => {
        console.log(response);
        if (response.status == 200) {
          MySwal.fire({
            title: "Registration",
            icon: "success",
            confirmButtonColor: "#000",
            text: "Successfully Registered!",
          });
          clearFields();
         history.push("/login");
        }
      })
      .catch((err) => {
        console.log(err);
        MySwal.fire({
          title: "Oops, Something went wrong.",
          icon: "error",
          text: err.response.data.message,
        });
        // this.$q.notify({
        //   type: "negative",
        //   icon: "error",
        //   message: `Error : ${err.response.data}`,
        // });
      });
  };
  useEffect(()=>{
    const getCountry = ()=> {
      axios({
        url: `${process.env.REACT_APP_API_URL}/api/invrel/country`,
  
        method: "get",
      })
        .then((response) => {
          console.log(response, "country");
          if (response.status == 200) {
            setCountryOpt(response.data)
            // return response.data
          }
        })
        .catch((err) => {});
    }
    getCountry()
  }, [])
  
  // getCountry()
  // render() {

  //   console.log(this.state.country_opt, "this.state.country_opt");
  return (
    <div className="support-content">
      <Row className="padding-50 ">
        <Col>
          <div className="text-subtitle1  text-center padding-10">
            <h3>
              <strong>Sign Up</strong>
            </h3>
            <h6>
              Already have an account ? <a href="/login">Sign In</a>
            </h6>
          </div>
          <Row>
            <div className="col-sm-6 offset-3 text-justify padding-10">
              <Form
                noValidate
                validated={validated}
                className="padding-50"
                style={{
                  border: "1px solid #ddd",
                  borderRadius: "10px",
                  background: "#ddd",
                }}
              >
                <Row>
                  <div className="col-sm-6 " style={{ padding: 0 }}>
                    <div
                      className="col-sm-12 text-center "
                      style={{ fontWeight: "bold" }}
                    >
                      BUSINESS PROFILE
                    </div>

                    <Form.Group className="padding-10">
                      <Form.Label>
                        Business Name <span className="text-red">*</span>{" "}
                      </Form.Label>
                      <Form.Control
                        required
                        onChange={(e)=>setBusinessName(e.target.value)}
                        value={businessName}
                        type="text"
                        placeholder="Business Name"
                      />
                      <Form.Control.Feedback type="invalid">
                        this field is required.
                      </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="padding-10">
                      <Form.Label>
                        Country of Origin <span className="text-red">*</span>{" "}
                      </Form.Label>
                      <Form.Control
                        as="select"
                        required
                        value={countryOrigin}
                        onChange={(e)=>setCountryOrigin(e.target.value)}
                        // placeholder="Country of Origin"
                      >
                        {
                          countryOpt.map((x, i) => (
                            <option value={x.name}>{x.name}</option>
                          ))}
                      </Form.Control>
                      <Form.Control.Feedback type="invalid">
                        this field is required.
                      </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="padding-10">
                      <Form.Label>
                        Business Address <span className="text-red">*</span>{" "}
                      </Form.Label>
                      <Form.Control
                      required
                        onChange={(e)=>setBusinessAddress(e.target.value)}
                        value={businessAddress}
                        type="text"
                        placeholder="Business Address"
                      />
                    </Form.Group>
                  </div>
                  <div className="col-sm-6">
                    <div
                      className="col-sm-12 text-center "
                      style={{ fontWeight: "bold" }}
                    >
                      CONTACT PERSON
                    </div>
                    <Form.Group
                      className="padding-10 "
                      style={{ display: "none" }}
                    >
                      <Form.Label>
                        Official Email Address{" "}
                        <span className="text-red">*</span>{" "}
                      </Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Official Email Address"
                      />
                    </Form.Group>
                    <Form.Group className="padding-10">
                      <Form.Label>
                        First Name <span className="text-red">*</span>{" "}
                      </Form.Label>
                      <Form.Control
                      required
                        onChange={(e)=>setFirstName(e.target.value)}
                        value={firstName}
                        type="text"
                        placeholder="First Name"
                      />
                    </Form.Group>
                    <Form.Group className="padding-10">
                      <Form.Label>
                        Last Name <span className="text-red">*</span>{" "}
                      </Form.Label>
                      <Form.Control
                      required
                        onChange={(e)=>setLastName(e.target.value)}
                        value={lastName}
                        type="text"
                        placeholder="Last Name"
                      />
                      <Form.Control.Feedback type="invalid">
                        this field is required.
                      </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="padding-10">
                      <Form.Label>
                        Contact Number <span className="text-red">*</span>{" "}
                      </Form.Label>
                      <Form.Control
                      required
                        onChange={(e)=>setContactNumber(e.target.value)}
                        value={contactNumber}
                        type="text"
                        placeholder="Contact Number"
                      />
                      <Form.Control.Feedback type="invalid">
                        this field is required.
                      </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="padding-10">
                      <Form.Label>
                        Email Address <span className="text-red">*</span>{" "}
                      </Form.Label>
                      <Form.Control
                      required
                        onChange={(e)=>setEmailAddress(e.target.value)}
                        value={emailAddress}
                        type="text"
                        placeholder="Email Address"
                      />
                      <Form.Control.Feedback type="invalid">
                        this field is required.
                      </Form.Control.Feedback>
                    </Form.Group>
                    {/* <Form.Group className="padding-10">
                        <Form.Label>
                          Password <span className="text-red">*</span>{" "}
                        </Form.Label>
                        <Form.Control
                          onChange={this.handleChangePassword}
                          value={this.state.password}
                          type="password"
                          placeholder="Password"
                        />
                      </Form.Group>
                      <Form.Group className="padding-10">
                        <Form.Label>
                          Re-type Password <span className="text-red">*</span>{" "}
                        </Form.Label>
                        <Form.Control
                          onChange={this.handleChangeReTypePassword}
                          value={this.state.reTypePassword}
                          type="password"
                          placeholder="Re-type Password"
                        />
                      </Form.Group> */}
                  </div>
                </Row>
              </Form>
              <div style={{ width: "100%" }} className="text-right margin-20">
                <button className=" cancel-btn" type="button">
                  Cancel
                </button>
                <Button onClick={signUp} className="login-btn" type="button">
                  next
                </Button>
              </div>
            </div>
            <div className="col-sm-2"></div>
          </Row>
        </Col>
      </Row>
    </div>
  );
  // }
}

export default Register;
