import "./Login.css";
import {
  Button,
  Col,
  Row,
  Card,
  Table,
  Image,
  Figure,
  Link,
  Form,
} from "react-bootstrap";
import axios from "axios";
import Swal from "sweetalert2";
import React, { Component } from "react";
import withReactContent from "sweetalert2-react-content";
import store from "../store";
// import LocalStorage from "localstorage";

const MySwal = withReactContent(Swal);
class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      emailAddress: "",
      password: "",
    };

    this.login = this.login.bind(this);


    this.handleChangeEmailAddress = this.handleChangeEmailAddress.bind(this);
    this.handleChangePassword = this.handleChangePassword.bind(this);
    this.clearFields = this.clearFields.bind(this);

  }
  handleChangeEmailAddress(event) {
    this.setState({ emailAddress: event.target.value });
  }
  handleChangePassword(event) {
    this.setState({ password: event.target.value });
  }
  clearFields() {
    this.setState({ emailAddress: "" });
    this.setState({ password: "" });
  }
 

  login() {
    axios({
      url:
        `${process.env.REACT_APP_API_URL}/api/invrel/login`,
      method: "post",
      // headers: {
      //   authorization: `Bearer ${this.$store.getters["auth/getToken"]}`,
      // },
      data: {
        email: this.state.emailAddress,
        passwd: this.state.password,
      },
    })
      .then((response) => {
        console.log(response);
        if (response.status === 200) {
          // const dispatch = useDispatch();

          store.dispatch({
            type: "SIGN_IN",
            payload: {
              email: response.data.email,
              token: response.data.token,
              domain: response.data.domain,
              isLoggedIn:true
            },
          });
         
          if (!response.data.domain || response.data.domain === "") {
            this.props.history.push("/setup/domain");
          }else{
            this.props.history.push("/redirect-to-auth");
          }

          this.clearFields();
        }
      })
      .catch((err) => {
        console.log(err);
        MySwal.fire({
          title: "Login",
          icon: "error",
          confirmButtonColor: "#000",
          text: err.response.data.messsage,
        });
      });
  }
  render() {
    return (
      <div className="support-content">
        <Row className="padding-50 ">
          <Col>
            <div className="text-subtitle1  text-center padding-10">
              <h3>
                <strong>Sign In</strong>
              </h3>
              <h6>
                Don't you have an account yet? <a href="/register">Sign Up</a>
              </h6>
            </div>
            <Row>
              <div
                style={{
                  border: "1px solid #ddd",
                  borderRadius: "10px",
                  background: "#ddd",
                }}
                className="col-sm-4 offset-4 text-justify padding-30"
              >
                <Form className="padding-50">
                  <Form.Group controlId="formBasicEmail" className="padding-10">
                    <Form.Label>
                      Email address <span className="text-red">*</span>{" "}
                    </Form.Label>
                    <Form.Control
                      onChange={this.handleChangeEmailAddress}
                      value={this.state.emailAddress}
                      type="email"
                      placeholder="Email Address"
                    />
                    {/* <Form.Text className="text-muted">
                    We'll never share your email with anyone else.
                  </Form.Text> */}
                  </Form.Group>

                  <Form.Group
                    controlId="formBasicPassword"
                    className="padding-10"
                  >
                    <Form.Label>
                      Password <span className="text-red">*</span>
                    </Form.Label>
                    <Form.Control
                      onChange={this.handleChangePassword}
                      value={this.state.password}
                      type="password"
                      placeholder="Password"
                    />
                  </Form.Group>
                  <Form.Group
                    controlId="formBasicCheckbox"
                    className="padding-10"
                  >
                    <Form.Text className="text-muted float-right">
                      <a href="">Forgot password ?</a>
                    </Form.Text>
                    <Form.Check
                      style={{ opacity: 0 }}
                      type="checkbox"
                      label="Keep me login"
                    />
                  </Form.Group>
                  <div style={{ width: "100%" }} className="text-center">
                    <Button
                      onClick={this.login}
                      variant="primary"
                      className="login-btn "
                      type="button"
                    >
                      Sign In
                    </Button>
                  </div>
                </Form>
              </div>
              <div className="col-sm-3"></div>
            </Row>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Login;
