import "./Home.css";
import { Button, Col, Row, Card, Table, Image, Figure } from "react-bootstrap";
function Home() {
  return (
    <div className="home-content ">
      <Row className="padding-50 border">
        <Col xs lg="5" className="margin-t-50 margin-b-50">
          <div className="text-title1 text-left">Create your website, </div>
          <div className="text-title1 text-left"> any time any where. </div>
          <div className="text-subtitle2 text-left">
            Be able to set up your website at your convenience.
          </div>
          <div className="call2action-div margin-t-30 text-left">
            <Button to="/register" className="calltoaction-1 btn btn-flat">Get Started</Button>
          </div>
        </Col>
        <Col className="margin-t-50 margin-b-70" >
          <Image
            className="irs-logo"
            src="../app.png"
            style={{ width: "600px" }}
          />
        </Col>
      </Row>
      <Row className="padding-50">
        <Col xs >
          <div className="text-subtitle1  text-center padding-10">
            <h5>
              <strong>EXPLORE OUR PRODUCT</strong>
            </h5>
          </div>
          <div className="margin-b-30">
            <iframe
              width="1520"
              height="485"
              src="https://www.youtube.com/embed/AOJdwzfbnHI"
              title="[1 Hour] Tokyo Ghoul OP/Opening Full「Unravel - TK]"
              frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              width="90%"
              allowfullscreen
            ></iframe>
          </div>
        </Col>
      </Row>
      <Row className="padding-50 ">
        <Col xs >
          <Row>
            <div  className="col-sm-1"></div>
            <div
              xs
              lg="6"
              style={{
                border: "1px solid #ddd",
                borderRadius: "10px",
                background: "#ddd",
              }}
              className="col-sm-10 text-justify padding-30"
            >
              <div className="text-subtitle1  text-center padding-10">
                <h5>
                  <strong>What is Content Management System</strong>
                </h5>
              </div>
              A Content Management System (CMS) helps companies manage digital
              content. Whole teams can use these systems to create, edit,
              organize, and publish content. It acts as a single place to store
              content and provides automated processes for collaborative digital
              content management and creation using built-in (or designed)
              workflows. Different privileges and responsibilities are provided
              to individuals based on roles. For example, authors can post and
              save their work, but editors can modify and publish it.
              Administrators can do all these things as well as grant other
              people in the organization permission to update or revise content.
            </div>
            <div  className="col-sm-1"></div>
          </Row>
        </Col>
      </Row>
      <Row classname="padding-50">
        <Col xs >
          <Row>
            <div  className="col-sm-1"></div>
            <div
              xs
              lg="6"
             
              className="col-sm-10 text-justify padding-30"
            >
              <Row>
                <Col>
                  <Image
                    className="irs-logo"
                    src="../apps2.png"
                    style={{ width: "500px" }}
                  />
                </Col>
                <Col>
                  <div className="text-subtitle1  text-left padding-10">
                    <h5>
                      <strong>Our product</strong>
                    </h5>
                  </div>
                  <p>
                    
                    <strong>SaaS</strong> CMS A SaaS CMS is a complete,
                    end-to-end solution. SaaS CMS is hosted in the cloud. This
                    means that it requires no actual setup, installation, or
                    preconfigured web hosting.
                  </p>
                  <p>
                    A SaaS-based CMS is an excellent solution for companies who
                    need a straightforward web presence, as it offers all the
                    capabilities without any of the server or web-hosting
                    overhead. It enables all kinds of users to quickly create
                    websites, manage the content, and distribute it through
                    digital channels.
                  </p>
                </Col>
              </Row>
            </div>
            <div  className="col-sm-1"></div>
          </Row>
        </Col>
      </Row>
      <Row className="padding-50">
        <Col xs >
          <Row>
            <div  className="col-sm-1"></div>
            <div
              xs
              lg="6"
              style={{
                border: "1px solid #ddd",
                borderRadius: "10px",
                background: "#ddd",
              }}
              className="col-sm-10 text-justify padding-30"
            >
              <div className="text-subtitle1  text-center padding-10">
                <h5>
                  <strong>Benefits</strong>
                </h5>
              </div>
              <Row>
                <Col className="padding-20 border margin-20 text-center ">
                  <Figure>
                    <Figure.Image
                      width={100}
                      height={100}
                      alt="171x180"
                      src="../launch.png"
                    />
                    <Figure.Caption className="text-left text-black">
                      <strong>Immediate Launch</strong>
                    </Figure.Caption>
                    <Figure.Caption className="text-left text-black">
                      Built with speed and efficiency in mind, reusable code
                      libraries, and instant content updates.
                    </Figure.Caption>
                  </Figure>
                </Col>
                <Col className="padding-20 border margin-20 text-center">
                  <Figure>
                     <Figure.Image
                    width={100}
                    height={100}
                    alt="171x180"
                    src="../location.png"
                  />
                    <Figure.Caption className="text-left text-black">
                      <strong>Benefits</strong>
                    </Figure.Caption>
                    <Figure.Caption className="text-left text-black">
                      Accessibility Accessible anytime and anywhere.
                    </Figure.Caption>
                  </Figure>
                </Col>
                <Col className="padding-20 border margin-20 text-center">
                  <Figure>
                  <Figure.Image
                      width={100}
                      height={100}
                      alt="171x180"
                      src="../star-user.png"
                    />
                    <Figure.Caption className="text-left text-black">
                      <strong> Personalised Digital Experience </strong>
                    </Figure.Caption>
                    <Figure.Caption className="text-left text-black">
                      Increase conversion and engagement, and other
                      personalization tools at your disposal, while reducing
                      redundancies in content.
                    </Figure.Caption>
                  </Figure>
                </Col>
              </Row>
            </div>
            <div  className="col-sm-1"></div>
          </Row>
        </Col>
      </Row>
      <Row className="padding-50">
        <div  className="col-sm-1"></div>
        <div className="col-sm-10 padding-20 ">
          <Row>
            <Col xs >
              <div className="text-left padding-10">
                <h3>
                  <strong>
                    Factors to consider in evaluating the best-fit CMS for your
                    business
                  </strong>
                </h3>
              </div>
              <div className="text-left padding-10">
                <strong>Content governance</strong>
                <p>
                  Are there enforced policies and procedures in place for
                  creating and managing content?
                </p>
                <strong>Information architecture for tagging</strong>
                <p>
                  Is current and new content categorized and tagged in a way
                  that everyone understands?
                </p>
                <strong>Business process for content management</strong>
                <p>Are workflows standardized and automated?</p>
                <strong>User experience for content</strong>
                <p>
                  
                  How are key stakeholders-from employees to customers-finding
                  the information they need?
                </p>
                <strong>Technology and applications</strong>
                <p>
                  Is the existing technology being used properly? What
                  improvements or new systems would streamline information flow
                  with optimal security? Do you have a cloud-based content
                  management system?
                </p>
                <strong>Business value of a CMS</strong>
                <p>
                  
                  Can your data and content be managed more efficiently and
                  securely? Are applicable regulatory guidelines being adhered
                  to?
                </p>
              </div>
            </Col>
          </Row>
        </div>
        <div  className="col-sm-1"></div>
      </Row>
    </div>
  );
}

export default Home;
