import logo from "./logo.svg";
import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Home from "./Home/Home";
import Support from "./Support/Support";
import Header from "./Header/Header";
import Footer from "./Footer/Footer";
import Login from "./Login/Login";
import Register from "./Register/Register";
import VerifyPassword from "./VerifyPassword/VerifyPassword";
import DomainSetup from "./DomainSetup/DomainSetup";
import axios from "axios";
import store from "./store";
import { AccordionButton } from "react-bootstrap";


function App() {
 
  return (
    <Router>
      <div className="App">
        <Header />
        <div className="content">
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route exact path="/support">
              <Support />
            </Route>
            <Route
              exact
              path="/login"
              render={(props) => <Login {...props} />}
            />
            <Route
              exact
              path="/register"
              render={(props) => <Register {...props} />}
            />
            <Route
              exact
              path="/verify/password/:email/:token"
              render={(props) => <VerifyPassword {...props} />}
            />
            <Route
              exact
              path="/setup/domain"
              render={(props) => <DomainSetup {...props} />}
            />
            <Route
              path="/redirect-to-auth"
              render={() => window.location.href = process.env.REACT_APP_CMS_URL}
            />
            <Route
              path="/logout"
              render={(props) => {
                store.dispatch({ type:"SIGN_OUT" });
                console.log(props, "props");
                props.history.push("/login");
              }}
            />

          
          </Switch>
        </div>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
