import "./Header.css";
import {
  Button,
  Alert,
  Col,
  Row,
  Nav,
  Image,
  DropdownButton,
  Dropdown,
} from "react-bootstrap";
import { useSelector } from "react-redux";
import { useState } from "react";
import axios from "axios";
import store from "../store";


function getCurrentUser() {
  const token = 
  window.localStorage.getItem("_token");
  if(!token)
  return;
  axios({
    url:
      `${process.env.REACT_APP_API_URL}/api/invrel/loggedin`,
    method: "post",
    headers: {
      authorization: `Bearer ${token}`,
    },
  }).then((response) => {
    if (response.status === 200) {
      console.log(response.data, "rsponse");
      response.data.isLoggedIn = true;
      store.dispatch({ type: "SIGN_IN", payload: response.data });
    }
  });
}


getCurrentUser();
function Header() {
 
  const { auth } = useSelector((state) => ({ ...state }));

  return (
    <Row className="header-nav ">
      <Col>
        <Row>
          <Col lg="3 header-logo-div">
            <Image
              className="irs-logo"
              src="../telcom-logo.png"
              style={{ height: "50px", width: "50px" }}
            />
          </Col>
          <Col className="header-title-div">
            <strong>Investor</strong>
            <br></br> <strong>Relations</strong>
          </Col>
        </Row>
      </Col>

      <Col>
        <Nav activeKey="/home">
          <Nav.Item className="nav-class">
            <Nav.Link href="/">Home</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link href="/support">TMCS Support</Nav.Link>
          </Nav.Item>
          {!auth.isLoggedIn && (
            <Nav.Item style={{ borderLeft: "1px dashed #ddd" }}>
              <Nav.Link href="/login">Login</Nav.Link>
            </Nav.Item>
          )}
          {!auth.isLoggedIn && (
            <Nav.Item>
              <Nav.Link href="/register">Register</Nav.Link>
            </Nav.Item>
          )}
          {auth.isLoggedIn && (
            <Nav.Item style={{ borderLeft: "1px dashed #ddd" }}>
              <Nav.Link>
                <i className="fa fa-user" />
                {auth.email}
              </Nav.Link>
            </Nav.Item>
          )}
          {auth.isLoggedIn && (
            <Nav.Item >
              <Nav.Link href="/logout">
               Logout
              </Nav.Link>
            </Nav.Item>
          )}
        </Nav>
      </Col>
    </Row>
  );
}

export default Header;
