import "./VerifyPassword.css";
import {
  Button,
  Col,
  Row,
  Card,
  Table,
  Image,
  Figure,
  Form,
} from "react-bootstrap";
import axios from "axios";
import Swal from "sweetalert2";
import React, { Component } from "react";

import withReactContent from "sweetalert2-react-content";

const MySwal = withReactContent(Swal);

class VerifyPassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      emailAddress: "",
      password: "",
      cpassword: "",
      verified: false,
      verify_token: "",
    };
    
    this.setUserPassword = this.setUserPassword.bind(this);

    this.handleChangeEmailAddress = this.handleChangeEmailAddress.bind(this);
    // this.redirectToLogin = this.redirectToLogin.bind(this);
    this.handleChangePassword = this.handleChangePassword.bind(this);
    this.handleChangeConfirmPassword =
      this.handleChangeConfirmPassword.bind(this);
    this.verifiedHandling = this.verifiedHandling.bind(this);
    this.clearFields = this.clearFields.bind(this);

    this.verifyToken().then((response) => {
      console.log(response, "verifyToken Res.");
      if (response.status == 200) {
        // resolve(response.data.status)
        // this.setState({ verified: true });
        this.verifiedHandling(response.data);
      }
      console.log(process.env.REACT_APP_API_URL); //printing it to console
    });
  }

  handleChangeEmailAddress(event) {
    this.setState({ emailAddress: event.target.value });
  }


  verifiedHandling(data) {
    this.setState({ verified: data.status == "Verified" ? true : false });
    this.setState({ verify_token: data.token });
    return;
  }
  handleChangePassword(event) {
    this.setState({ password: event.target.value });
  }
  handleChangeConfirmPassword(event) {
    this.setState({ cpassword: event.target.value });
  }
  clearFields() {
    this.setState({ emailAddress: "" });
    this.setState({ password: "" });
  }

  setUserPassword(){
   
   
    // return
    if (this.state.password != this.state.cpassword) {
      MySwal.fire({
        title: "Error",
        icon: "error",
        confirmButtonColor: "#000",
        text: "Password does not match",
      });
      return;
    }
    axios({
      url:
        `${process.env.REACT_APP_API_URL}/api/invrel/register`,
      method: "post",
      headers: {
        Authorization: `Bearer ${this.state.verify_token}`,
      },
      data: {
        email: this.props.match.params.email,
        passwd: this.state.password,
      },
    })
      .then((response) => {
        console.log(response);
        if (response.status == 200) {
          MySwal.fire({
            title: "Registered",
            icon: "success",
            confirmButtonColor: "#000",
            text: "Pasword is set!",
          });
          this.clearFields();
          this.props.history.push('/login')
        }
      })
      .catch((err) => {
        console.log(err);
        MySwal.fire({
          title: "Login",
          icon: "error",
          confirmButtonColor: "#000",
          text: err.response.data.message,
        });
      });
  }
  async verifyToken(event) {
    return await axios({
      url:
        `${process.env.REACT_APP_API_URL}/api/invrel/${this.props.match.params.email}/${this.props.match.params.token}/validate`,
      method: "get",
    });
  }

  render() {
    console.log(process.env.REACT_APP_API_URL);

    let page;
    if (this.state.verified) {
      page = (
        <Row className="padding-50 margin-b-50">
          <Col>
            <div className="text-subtitle1  text-center padding-10">
              <h3>
                <strong>SETUP YOUR PASSWORD</strong>
              </h3>
            </div>
            <Row>
              <div
                style={{
                  border: "1px solid #ddd",
                  borderRadius: "10px",
                  background: "#ddd",
                }}
                className="col-sm-4 offset-4 text-justify padding-30"
              >
                <Form className="padding-50">
                  <Form.Group
                    controlId="formBasicPassword"
                    className="padding-10"
                  >
                    <Form.Label>
                      New Password <span className="text-red">*</span>
                    </Form.Label>
                    <Form.Control
                      onChange={this.handleChangePassword}
                      value={this.state.password}
                      type="password"
                    />
                  </Form.Group>
                  <Form.Group
                    controlId="formBasicPassword"
                    className="padding-10"
                  >
                    <Form.Label>
                      Confirm Password <span className="text-red">*</span>
                    </Form.Label>
                    <Form.Control
                      onChange={this.handleChangeConfirmPassword}
                      value={this.state.cpassword}
                      type="password"
                    />
                  </Form.Group>

                  <div style={{ width: "100%" }} className="text-center">
                    <Button
                      onClick={this.setUserPassword}
                      variant="primary"
                      className="login-btn "
                      type="button"
                    >
                      Set Password
                    </Button>
                  </div>
                </Form>
              </div>
              <div className="col-sm-3"></div>
            </Row>
          </Col>
        </Row>
      );
    } else {
      page = (
        <div className="padding-50 margin-50">
          <h1 className="padding-50 margin-50">NOT AUTHORIZED</h1>
        </div>
      );
    }
    return <div className="support-content">{page}</div>;
  }
}

export default VerifyPassword;
