import "./DomainSetup.css";
import { Button, Col, Row, InputGroup, Form } from "react-bootstrap";
import axios from "axios";
import Swal from "sweetalert2";
import React, { Component } from "react";
import withReactContent from "sweetalert2-react-content";
import store from "../store";
const MySwal = withReactContent(Swal);
class DomainSetup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      setup: "setup1",
      dns: "",
      dns1: "",
      dns2: "",
      dns3: "",
      dns4: "",
    };
    this.proceed = this.proceed.bind(this);
    this.checkAvailability = this.checkAvailability.bind(this);

    this.handleChangeSetup = this.handleChangeSetup.bind(this);
    this.handleChangeDNS = this.handleChangeDNS.bind(this);
    this.handleChangeDNS1 = this.handleChangeDNS1.bind(this);
    this.handleChangeDNS2 = this.handleChangeDNS2.bind(this);
    this.handleChangeDNS3 = this.handleChangeDNS3.bind(this);
    this.handleChangeDNS4 = this.handleChangeDNS4.bind(this);
    this.clearFields = this.clearFields.bind(this);
  }
  handleChangeSetup(event) {
    this.setState({ setup: event.target.value });
  }
  handleChangeDNS(event) {
    this.setState({ dns: event.target.value });
  }
  handleChangeDNS1(event) {
    this.setState({ dns1: event.target.value });
  }
  handleChangeDNS2(event) {
    this.setState({ dns2: event.target.value });
  }
  handleChangeDNS3(event) {
    this.setState({ dns3: event.target.value });
  }
  handleChangeDNS4(event) {
    this.setState({ dns4: event.target.value });
  }
  clearFields() {
    this.setState({ emailAddress: "" });
    this.setState({ password: "" });
  }

  checkAvailability() {
    MySwal.fire("validating...");
    // this.props.history.push("/tothemoon");
  }

  proceed() {
    axios({
      url: `${process.env.REACT_APP_API_URL}/api/invrel/domain`,
      method: "post",
      headers: {
        Authorization: `Bearer ${store.getState().auth.token}`,
      },
      data: {
        domain: this.state.dns,
      },
    })
      .then((response) => {
        console.log(response);
        if (response.status === 200) {
          this.props.history.push("/redirect-to-auth");
          this.clearFields();
        }
      })
      .catch((err) => {
        console.log(err.response.data);
        MySwal.fire({
          title: "Login",
          icon: "error",
          confirmButtonColor: "#000",
          text: err.response.data.messsage,
        });
      });
  }
  render() {
    return (
      <div className="support-content">
        <Row className="padding-50 ">
          <Col>
            <div className="text-subtitle1  text-center padding-10">
              <h3>
                <strong>SETUP DNS</strong>
              </h3>
              <h6>Create A Domain for your website</h6>
            </div>
            <Row>
              <div
                style={{
                  border: "1px solid #ddd",
                  borderRadius: "10px",
                  background: "#ddd",
                }}
                className="col-sm-8 offset-2 text-justify padding-30"
              >
                <Form className="padding-50">
                  <Form.Group
                    controlId="formBasicPassword"
                    className="padding-10"
                  >
                    <Form.Label>
                      Select DNS Setup <span className="text-red">*</span>
                    </Form.Label>
                    <Form.Control
                      as="select"
                      value={this.state.setup}
                      onChange={this.handleChangeSetup}
                    >
                      <option value="setup1">
                        Do you already have DNS for Investor Relation ?
                      </option>
                      <option value="setup2">
                        {" "}
                        Do you want to register a new Domain ?
                      </option>
                      <option value="setup3">
                        Would you like to use our Domain ?
                      </option>
                    </Form.Control>
                  </Form.Group>
                  {this.state.setup === "setup1" && (
                    <Form.Group
                      controlId="formBasicPassword"
                      className="padding-10"
                    >
                      <Form.Label>
                        Please enter your preffered domain.{" "}
                        <span className="text-red">*</span>
                      </Form.Label>
                      <Form.Control
                        onChange={this.handleChangeDNS}
                        value={this.state.dns}
                        placeholder="www.sampledomainname.com.ph"
                      />
                    </Form.Group>
                  )}
                  {this.state.setup === "setup2" && (
                    <div>
                      <Form.Group
                        controlId="formBasicPassword"
                        className="padding-10"
                      >
                        <Form.Label>
                          Please enter your DNS.{" "}
                          <span className="text-red">*</span>
                        </Form.Label>
                        <Form.Control
                          onChange={this.handleChangeDNS1}
                          value={this.state.dns1}
                          placeholder="www.sampledomainname.com.ph"
                        />
                      </Form.Group>

                      <Form.Group
                        controlId="formBasicPassword"
                        className="padding-10"
                      >
                        <Form.Label>
                          Please enter your DNS.{" "}
                          <span className="text-red">*</span>
                        </Form.Label>
                        <Form.Control
                          onChange={this.handleChangeDNS2}
                          value={this.state.dns2}
                          placeholder="www.sampledomainname.com.ph"
                        />
                      </Form.Group>

                      <Form.Group
                        controlId="formBasicPassword"
                        className="padding-10"
                      >
                        <Form.Label>
                          Please enter your DNS.{" "}
                          <span className="text-red">*</span>
                        </Form.Label>
                        <Form.Control
                          onChange={this.handleChangeDNS3}
                          value={this.state.dns3}
                          placeholder="www.sampledomainname.com.ph"
                        />
                      </Form.Group>
                    </div>
                  )}
                  {this.state.setup === "setup3" && (
                    <Form.Group
                      controlId="formBasicPassword"
                      className="padding-10"
                    >
                      <Form.Label>
                        Please enter your DNS.{" "}
                        <span className="text-red">*</span>
                      </Form.Label>
                      <div class="input-group mb-3">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="www.mysubdomain"
                          aria-label="Recipient's username"
                          aria-describedby="basic-addon2"
                        />
                        <div className="input-group-append">
                          <span className="input-group-text" id="basic-addon2">
                            .tlcpay.ph
                          </span>
                        </div>
                      </div>
                    </Form.Group>
                  )}
                  {/* #2 */}

                  <div style={{ width: "100%" }} className="text-right">
                    {this.state.setup === "setup2" && (
                      <Button
                        onClick={this.checkAvailability}
                        variant="primary"
                        className="login-btn"
                        style={{ width: "150px", float: "right" }}
                        type="button"
                      >
                        Check Availability
                      </Button>
                    )}
                    <Button
                      onClick={this.proceed}
                      variant="primary"
                      className="login-btn"
                      type="button"
                    >
                      Proceed
                    </Button>
                  </div>
                </Form>
              </div>
              <div className="col-sm-3"></div>
            </Row>
          </Col>
        </Row>
      </div>
    );
  }
}

export default DomainSetup;
