import "./Support.css";
import { Button, Col, Row, Card, Table, Image, Figure } from "react-bootstrap";
function Support() {
  return (
    <div className="support-content">
      <Row className="support-banner-div"></Row>
      <Row className="padding-50 ">
        <Col xs>
          <div className="text-subtitle1  text-center padding-10">
            <h3>
              <strong>TCMS SUPPORT</strong>
            </h3>
          </div>
          <Row>
            <div className="col-sm-1"></div>
            <div
              xs
              lg="6"
              style={{
                border: "1px solid #ddd",
                borderRadius: "10px",
                background: "#ddd",
              }}
              className="col-sm-10 text-justify padding-30"
            >
              <div className="text-subtitle1  text-center padding-10">
                <h6>
                  <strong>
                    With TCMS Support, you are supported 24 hours a day, 7days a
                    week for critical issues that stops your system from running
                  </strong>
                </h6>
              </div>
              <p>
                Our trained support team are on hand for any request, no matter
                how large or small during normal business hours – Monday to
                Friday between <strong>8:30 am and 5:30 pm.</strong>
              </p>
              <p>
                {" "}
                We know that your business operates <strong>24 hours a day</strong>, and out of
                normal office hours our late night support team are available
                for those "mission critical" issues that stops your system from
                running.
              </p>

              <Row>
                <div className="col-sm-2">
                  <Image
                    className="irs-logo padding-10"
                    src="../callsupport.png"
                    style={{ width: "120px" }}
                  />
                </div>
                <div className="col-sm-10 text-left padding-30">
                  <strong>For existing customers</strong> log into our support ticketing system
                  to view the product knowledgebase or to log a new ticket and
                  to review your previous tickets, click here: <br />
                  <Button className="calltoaction-2 btn btn-flat margin-t-20">Team Support</Button>
                </div>
              </Row>
              <Row>
                <div className="col-sm-2">
                  <Image
                    className="irs-logo padding-10"
                    src="../systemsupport.png"
                    style={{ width: "120px" }}
                  />
                </div>
                <div className="col-sm-10 text-left padding-30">
                  <strong>If you require remote support</strong> from one of our technicians
                  please download and run the quick-support application here: <br />
                  <Button className="calltoaction-2 btn btn-flat margin-t-20">Team Viewer</Button>
                </div>
              </Row>
            </div>
            <div className="col-sm-1"></div>
          </Row>
        </Col>
      </Row>
      <Row className="padding-50">
        <Col>
          <Row>
            <div className="col-sm-1"></div>
            <div className="col-sm-10">
              <div className="text-subtitle1 col-sm-12 text-center margin-b-30 padding-10">
                <h3>
                  <strong>Service Level Agreement</strong>
                </h3>
              </div>
              <Row>
                <div className="col-sm-6 padding-10 border">
                  <strong>Serverity</strong>{" "}
                </div>
                <div className="col-sm-6 padding-10 border">
                  <strong>SLA</strong>{" "}
                </div>

                <div style={{background:"#dde6ff"}} className="col-sm-6 padding-10 border">Cosmetics</div>
                <div className="col-sm-6 padding-10 border">
                  Less Than 30 minutes
                </div>

                <div style={{background:"#adc3ff"}} className="col-sm-6 padding-10 border">Minor</div>
                <div className="col-sm-6 padding-10 border">
                  Less Than 1 hour
                </div>

                <div style={{background:"#ff6e6e"}} className="col-sm-6 padding-10 border">Moderate</div>
                <div className="col-sm-6 padding-10 border">
                  Less Than 3 hour
                </div>

                <div style={{background:"#ff0000"}} className="col-sm-6 padding-10 border">Major</div>
                <div className="col-sm-6 padding-10 border">
                  Less Than 1 day
                </div>
                <div style={{background:"#b70000"}} className="col-sm-6 padding-10 border">Critical</div>
                <div className="col-sm-6 padding-10 border">
                  Less Than 2 day
                </div>
              </Row>
            </div>
            <div className="col-sm-1"></div>
          </Row>
        </Col>
      </Row>
    </div>
  );
}

export default Support;
