import "./Footer.css";
import { Button, Alert, Col, Row, Nav, Image } from "react-bootstrap";
import { MDBCol, MDBContainer, MDBRow, MDBFooter } from "mdbreact";
function Footer() {
  return (
    <MDBFooter color="blue" className="font-small pt-4 mt-4 footer-div padding-30">
      <MDBContainer fluid className="text-center text-md-left">
        <MDBRow>
          <MDBCol md="4 margin-t-20">
            <h5 className="title">Investor Relations</h5>
            <p>
              Our Services
            </p>
            <ul>
              <li className="list-unstyled">
               Software Development
              </li>
              <li className="list-unstyled">
               Cloud Services
              </li>
              <li className="list-unstyled">
               Licensing
              </li>
              <li className="list-unstyled">
               Mobile Commerce
              </li>
              <li className="list-unstyled">
               Content Management System
              </li>
            </ul>
          </MDBCol>
          <MDBCol md="4 margin-t-20">
            <p className="title">About Us</p>
         
            <ul>
              <li className="list-unstyled">
               Board Trustees
              </li>
              <li className="list-unstyled">
               Officers
              </li>
              <li className="list-unstyled">
               Committee Composition
              </li>
              <li className="list-unstyled">
              Governance Documents
              </li>
              
            </ul>
          </MDBCol>
          <MDBCol md="4 margin-t-20">
            <p className="title">Location and Contact Numbers</p>
            <ul>
              <li className="list-unstyled">
               NCR office
              </li>
              <li className="list-unstyled">
               Bacolod Office
              </li>
              <li className="list-unstyled">
               Contact Numbers
              </li>
              <li className="list-unstyled">
              Email Address
              </li>
              
            </ul>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
      <div className="footer-copyright text-left 3">
        <MDBContainer fluid>
          &copy;  2015 TELCOM LIVE CONTENT. ALL RIGHT RESERVED
        </MDBContainer>
      </div>
    </MDBFooter>
  );
}

export default Footer;
